.App {
  /* height: calc(100vh - 40px); */
  height: calc(100% - 40px);
  height: 100%;
  width: 100%;
  background-color: green;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
}
.appInner{
  height: 90%;
  width: 90%;
  position: absolute;
  bottom: 5%;
  left: 5%;
  background-color: rgba(128, 128, 128, 0.534);
  overflow-y: auto;
  border: 2px solid black;
}
.appScroller{
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: green;
  overflow-y: auto;
}
img{
  height: 100%;
  width: 100%;
}