.homePage{
    width: 100%;
    height: 100%;
}
.spacer{
    height: 2000px;
}






.banner{
    height: 100%;    
    width: 100%;
    /* height: 200px; */
    overflow: hidden;
    text-align: center;   
    position: relative;
     /* Starting image for when the video is loading  */
    background-image: url("../../Media/bannerImageBeforeVideo.jpg");
    background-position: center;
    object-fit: cover;
    /* background-color: orange; */
}
.bannerVideo{
    object-position: 30%;
    width: 100% !important;
    height: 100% !important;
    object-fit: cover !important;
    display: inline-block;
}
.bannerText{
    width: 90%;
    position: absolute;
    bottom: 10px;   
    left: 5%;
    text-align: center;  
    color: white; 
    font-size: 30px;
    display: inline-block;
}
.goldLetters{
    color: #FFE170;
    /* background-color: rgba(248, 217, 103, 0.14); */
    box-shadow: 0 0 20px #fcdb661c;
    border-radius: 25%;
    padding: -10px 10px;
    background-image: radial-gradient(rgba(255, 217, 0, 0.31), rgba(255, 217, 0, 0.1), rgba(94, 90, 66, 0))
}





.banner h1{
    margin: 0;
}
.banner h3{
    margin: 0;
}
.bannerText1{
    font-size: 50px;
}
.bannerText1Mobile{
    font-size: 35px;
    /* line-height: 35px; */
}
.bannerText2{
    padding: 10px;
    box-sizing: border-box;
}
.bannerText2Mobile{
    padding: 10px;
    box-sizing: border-box;
    font-size: 20px;
}
.bannerText3{
    padding: 10px;
    box-sizing: border-box;
    font-size: 20px;
    text-align: justify;
}
.bannerText3Mobile{
    font-size: 16px;
}
.bannerButton{    
    padding: 15px 40px;    
    background-color: #D65E4D;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    transition: 500ms;
    margin: 36px;
    display: inline-block;    
    font-size: 20px;
}
.contentBoxButton{    
    padding: 15px 40px;    
    background-color: #D65E4D;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    transition: 500ms;
    display: inline-block;    
    font-size: 20px;
    position: absolute;
    bottom: 10px;
    width: 90%;
    left: 5%;
    box-sizing: border-box;
}
.bannerButton:hover{
    background-color: rgb(37, 10, 100);
    box-shadow: 0 0 20px #f3a126b0;
}

.butterflySection{
    background-attachment: fixed;    
    color: white;
    padding: 70px 140px;
    box-sizing: border-box;
    position: relative;
}
.foundationStatementTopMobile{
    position: absolute;
    top: -60px;
    /* height: 40px;
    width: 40px; */
    /* background-color: green; */
    z-index: 100;
}
.orangeButterflySection{
    background: url("../../Media/butterflyOrange.jpg");
    background-attachment: fixed;    
}
.purpleButterflySection{
    background: url("../../Media/butterflyPurple.jpg");
    background-attachment: fixed;    
}
.purpleButterflySectionMobile{
    padding-top: 0px;
    background: url("../../Media/butterflyPurple.jpg");
    background-size: contain;
    background-attachment: fixed;
}
.butterflySectionTitleArea{
    padding: 20px;
}
.noBackgroundSection{
    min-height: 200px;
    width: 100%;
    background-color: #F7F7F7;
}

.contentBox{
    display: inline-block;
    width: calc(50% - 40px);
    height: 360px;
    margin: 20px;
    background-color: white;
    vertical-align: top;
    padding: 20px;
    padding-bottom: 40px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    
}


.contentBox h2{
    color: #D65E4D;
}
.contentBox p{
    color: black;
    font-size: 20px;
    text-align: justify;
    margin-bottom: 40px;
    padding: 0 10px;
}
.contentBoxMobile{
    height: auto;
}
.contentBoxMobile h2{
    height: auto;
    margin: 0;
    font-weight: 500;
    font-size: 22px;
}
.contentBoxMobile p{
    padding-bottom: 20px;
    font-size: 16px;
    line-height: 25px;
}
.circleInfoArea{
    padding-top: 120px;
}
.circleInfoAreaMobile{
    padding: 20px 0;
}
.circleInfoBox{
    width: 100%;
    padding: 40px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -80px;
}
.circleInfoBoxMobile{
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    justify-content: center;
    min-height: 16em;
    position: relative;
    text-align: left;
    text-align: center;
}
.circleInfoBoxMobile img{
    height: 18em;
    width: 18em;
    border-radius: 50%;
}
.circleInfoBoxMobileTextBox{
    width: calc(100% - 40px);
    background-color: white;
    min-height: 30px;
    display: inline-block;
    position: relative;
    z-index: 2;
    margin-top: -4em;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
    padding: 10px;
}
.circleInfoBoxMobileTextBoxRight{
    right: 10px;
    left: auto;

}
.box{
    background-color: white;
    width: 600px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    display: inline-block;
    vertical-align: top;
    padding: 20px;
    box-sizing: border-box;
    z-index: 2;
    text-align: left;
}
.boxMobile{
    width: 80%

}
.rightOverlap{
    margin-right: -120px;

}
.leftOverlap{
    margin-left: -120px;

}
.circleInfoBox .title{
    color: #D65E4D;
    font-size: 30px;
}
.circleInfoBox .titleMobile{
    color: #D65E4D;
    font-size: 30px;
    margin: 0;
}
.circleInfoBox .content{
    color: black;

}
.circleImage{
    border-radius: 50%;
    overflow: hidden;
    width: 500px;
    height: 500px;
    display: inline-block;
    vertical-align: top;
}
.circleImageMobile{
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    vertical-align: top;
    object-fit: cover;
}
.circleImage img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.purpleSection{
    height: 40px;
    width: 100%;
    background-color: rgb(37, 10, 100);
}
.quoteBox{
    height: 600px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../Media/ladyWithPicture.jpg");
    background-attachment: fixed;
    background-size: cover;
}   
.quoteBoxMobile{
    height: 250px;
}
.quoteOverlay{
    background-color: #39215b65;
    position: absolute;
    height: 600px;
    width: 100%;
}
.quoteOverlayMobile{
    height: 250px;
}
.quoteBox img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.quote{
    position: absolute;
    font-size: 30px;
    font-style: italic;
    color: white;
    width: 60%;
}
.buttonContainer{
    width: 100%;
    text-align: center;    
}
.buttonContainer .bannerButton{
    margin: 0;
}
.bottomBar{
    background: url("../../Media/butterflyPurple.jpg");
    background-attachment: fixed;
    height: 300px;
    width: 100%;
    position: relative;
}
.bottomBarMobile{
    background-position: center;
    background-size: cover;
    height: auto;
}
.bottomOverlay{
    background-color: #23133abd;
    position: absolute;
    height: 100%;
    width: 100%;
}
.bottomBarLinks{
    z-index: 20;
    display: inline-block;
    /* position: absolute; */
    width: 100%;
    /* left: 0;
    top: 0; */
}
.bottomBarLinksMobile{

}
.bottomBarColumn{
    width: 30%;
    padding: 20px;
    padding-top: 40px;
    box-sizing: border-box;
    display: inline-block;
}
.bottomBarColumnMobile{
    width: 50%;
    vertical-align: top;
    padding: 0;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.5);
}
.bottomBarLink{
    color: white;
    display: block;
    padding: 20px;
    text-decoration: none;
    font-size: 20px;
    transition: 500ms;
}
.bottomBarLink:hover{
    color: rgb(182, 89, 182);
}
.topNavPlaceholder{
    position: fixed;
    top: 0;
    height: 60px;
    left: 0;
    width: 100%;
    background-color: white;
    z-index: 3;
}
.topNavPlaceholderTop{
    top: 0;
    bottom: auto;
}
.topNavPlaceholderBottom{
    top: auto;
    bottom: 0;
}

@media screen and (max-width: 1500px) {
    .contentBox{
        /* height: 420px */
        
    }   
}
@media screen and (max-width: 1150px) {
    .contentBox{
        width: calc(100% - 40px);
        
    }   
}
@media screen and (max-width: 950px) {
    .butterflySection{
        padding: 20px 20px;
    }   
    .purpleButterflySectionMobile{
        padding: 10px 20px;
        padding: 5px;

    }
}